import React, { useRef, useCallback, useEffect, useState } from 'react'

import { pdf } from '@react-pdf/renderer' 

import PDF from './PDF'



function usePDF(props) {
    const [loading, setLoading] = useState(true);
    const [documentURL, setDocumentURL] = useState('');

    useEffect(() => {
        const generateBlob = async () => {
            let blob;
            let retryAttempts = 0;
            setLoading(true);
      
            do{
                try { blob = await pdf(<PDF props={props} />).toBlob(); }
                catch (error){
                    await new Promise((resolve) => setTimeout(resolve, 500));
                    retryAttempts++;
                }
            }while(!blob && retryAttempts < 3);
            
            setDocumentURL(window.URL.createObjectURL(blob));
            setLoading(false);
        };

        if(props){ generateBlob(); }
          
    }, [props])

    return { loading, documentURL }
}

function CreatePDF(props) {

    const [ref] = useHookWithRefCallback()
    const { loading, documentURL } = usePDF(props)

    function useHookWithRefCallback() {
        const ref = useRef(null)
        const setRef = useCallback(node => {
            if(node){ 
                node.click() 
                props.done()
            }
            ref.current = node
        }, [])
        return [setRef]
    }

    return (
        !loading && (
            <a 
                href={documentURL} 
                ref={ref}
                download={`Resultados ${props.name} ${props.servname}.pdf`}
                style={{ display: 'none' }}
            >
            </a>
        )
    )
}

export default CreatePDF
