import React, { useState, useEffect } from 'react'
import axios from 'axios';

import InfoDate from '../user/Info'
import Recipe from './Form'
import InfoContainer from './InfoContainer'
import TokenExpirate from '../TokenExpirate'

import { Content, MessageError } from '../../styles/Admin'

import { URL } from '../../variables'
import Results from './Results';

function View({ userType }) {
    
    const [date, setDate] = useState({})
    const [recipe, setRecipe] = useState([])
    const [params, setParams] = useState([])
    const [results, setResults] = useState([])
    const [services, setServices] = useState([])
    const [err, setErr] = useState(false)
    const [tokenExpirate, setTokenExpirate] = useState(false)
    const [message, setMessage] = useState('')
    const [id, setId] = useState('')

    const getDate = async id => {
        const res = await axios({
            url: `${URL}/cita/${id}`,
            method: 'GET'
        })

        return res.data
    }

    const createRecipe = () => setRecipe(!recipe)

    const setLabsName = dates => {
        let newDates = dates
        setDate({...newDates[0], servnombre: newDates.map(item => item.servnombre).join(', ')})
    }

    useEffect(() => {
        let id = window.location.pathname.split('/')[ window.location.pathname.split('/').length - 1 ]
        getDate(id).then( ({ date, recipe, params, results }) => {
            setId(id)
            if(userType === "LAB"){ 
                setResults(results)
                setParams(params)
                setServices(date)
                setLabsName(date) 
                return
            }
            setDate(date[0])
            setRecipe(recipe)
        }).catch( e => {
            console.log(e)
            if(e.response?.status === 401){ return setTokenExpirate(true) }
            setErr(true)
            setMessage('Ha ocurrido un error intentado obtener esta cita. Intentalo más tarde o reporta el problema')
        })
    }, []) 

    return (
        <Content 
            alignItems="center" 
            justifyContent="space-evenly" 
            flexDirection="column" 
            width="100%"
        >
            {err && <MessageError>{message}</MessageError>}
            <InfoContainer userType={userType}>
                <InfoDate   
                    user={date}
                    fullDescription
                    fontSize="1.2vw"
                    userType={userType}
                    createRecipe={createRecipe}
                    admin
                />
            </InfoContainer>
            {userType === "MEDICO" ? (
                <Recipe 
                    id={id}
                    recipe={recipe}
                />
            ) : userType === "LAB" && (
                <Results 
                    id={id}
                    params={params}
                    lastResults={results}
                    date={date}
                    services={services}
                />
            )}
            <TokenExpirate modal={tokenExpirate} admin/>
        </Content>
    )
}

export default View
