import React, { useState, useEffect } from 'react'

function ResultItem({ param, onChange, idx, handleInputMax, handleInputMin, lastResults, results }) {

    const [result, setResult] = useState('')
    const [minRef, setMinRef] = useState('')
    const [maxRef, setMaxRef] = useState('')

    const getText = (e) => {
        if(param.tipo === "INT"){ return e.target.value.replace(/[^0-9]/g, '') }
        else{ return e.target.value}
    }

    const handleInputMinRef = e => {
        const text = getText(e)
        setMinRef(text)
        handleInputMin(idx, text)
    }

    const handleInputMaxRef = e => {
        const text = getText(e)
        setMaxRef(text)
        console.log('a')
        handleInputMax(idx, text)
    }

    const handleInputResult = e => {
        const text = getText(e)
        setResult(text)
        onChange(idx, text)
    }

    useEffect(() => {
        setResult('')
        if(typeof param.minRef !== "undefined"){ 
            setMinRef(param.minRef) 
            handleInputMin(idx, param.minRef)
        }
        if(typeof param.maxRef !== "undefined"){ 
            setMaxRef(param.maxRef) 
            handleInputMax(idx, param.maxRef)
        }
    }, [param])

    useEffect(() => {
        const value = lastResults.find( result => result?.param === param.id )
        if(value){
            setResult(value.resultado)
            onChange(idx, value.resultado)
            if(value.minRef){
                setMinRef(value.minRef)
                handleInputMin(idx, value.minRef)
            }if(value.maxRef){
                setMaxRef(value.maxRef)
                handleInputMax(idx, value.maxRef)
            }
        }
    }, [lastResults, param])

    return (
        <tr>
            <td>{param.nombre}</td>
            <td>
                <input 
                    id={`input${idx}`}
                    value={result}
                    onChange={handleInputResult}
                />
                {param.unidades}
            </td>
            <td>
                {param.tipo === "INT" && (
                    <>
                    <input 
                        id={`input${idx}-refmin`}
                        value={minRef}
                        onChange={handleInputMinRef}
                    />
                    -
                    <input 
                        id={`input${idx}-refmin`}
                        value={maxRef}
                        onChange={handleInputMaxRef}
                    />
                    {param.unidades}
                    </>
                )}
                
            </td>
        </tr>
    )
}

export default ResultItem
