import React, { useState, useEffect } from 'react'
import axios from 'axios'

import InfoDate from '../user/Info'
import InfoContainer from '../dateInfo/InfoContainer'
import TokenExpirate from '../TokenExpirate'
import History from './History'
import Graphics from './Graphics'

import { Content, MessageError } from '../../styles/Admin'

import { URL } from '../../variables'

function Patient({ userType, scrollToBottom }) {

    const [patient, setPatient] = useState({})
    const [dates, setdates] = useState([])
    const [paramsGraphics, setParamsGraphics] = useState([])
    const [resultsGrapichs, setResultsGrapichs] = useState([])
    const [message, setMessage] = useState('')
    const [servname, setServname] = useState('')
    const [err, setErr] = useState(false)
    const [tokenExpirate, setTokenExpirate] = useState(false)
    const [getDatesControl, setGetDatesControl] = useState(true)
    const [graphics, setGraphics] = useState(false)

    const getUser = async (id) => {
        const res = await axios({
            url: `${URL}/admin/client?id=${id}`,
            method: 'GET'
        })
        return res.data
    }

    const getDates = async (id) => {
        const res = await axios({
            url: `${URL}/cita/patients?user=${id}&start=${dates.length}`,
            method: 'GET'
        })
        return res.data
    }

    const fetchDates = id => {
        getDates(id).then(({ dates: datesFetched }) => {
            if(datesFetched.length === 0){ return setGetDatesControl(false) }
            setdates([...dates,  ...datesFetched])
        }).catch(e => {
            console.log(e)
            if(e.response?.status === 401){ return setTokenExpirate(true) }
            setErr(true)
            setMessage('Ha ocurrido intentado obtener el historial de este cliente. Intentalo más tarde o reporta el problema')
        })
    }

    const seeGraphics = (params, results, servname) => {
        setParamsGraphics(params)
        setResultsGrapichs(results)
        setServname(servname)
        setGraphics(true)
    }

    const closeGraphics = () => setGraphics(false)

    useEffect(() => {
        const id = window.location.pathname.split('/')[4]
        getUser(id).then(({ user }) => {
            setPatient(user)
            fetchDates(id)
        }).catch(e => {
            console.log(e)
            if(e.response?.status === 401){ return setTokenExpirate(true) }
            setErr(true)
            setMessage('Ha ocurrido intentado obtener la información de este cliente. Intentalo más tarde o reporta el problema')
        })
    }, [])

    useEffect(() => {
        if(scrollToBottom && getDatesControl){
            const id = window.location.pathname.split('/')[4]
            fetchDates(id)
        }
    }, [scrollToBottom])

    return (
        <Content 
            alignItems="center" 
            justifyContent="space-evenly" 
            flexDirection="column" 
            width="100%"
        >
            {err && <MessageError>{message}</MessageError>}
            <InfoContainer userType={userType} title={graphics ? 'Gráficos' : 'Historial'} patient>
                <InfoDate   
                    user={patient}
                    fullDescription
                    fontSize="1.2vw"
                    userType={userType}
                    admin
                />
            </InfoContainer>
            {!graphics ? (
                <History 
                    dates={dates} 
                    setErr={setErr}   
                    setMessage={setMessage}
                    setTokenExpirate={setTokenExpirate}
                    user={patient}
                    seeGraphics={seeGraphics}
                />
            ):(
                <Graphics 
                    params={paramsGraphics}
                    results={resultsGrapichs}
                    servname={servname}
                    closeGraphics={closeGraphics}
                />
            )}
            <TokenExpirate modal={tokenExpirate} admin/>
        </Content>
    )
}

export default Patient
