import React from 'react'
import Select from 'react-select';

import { InputContainer, InputForm, TextareaForm ,InputLabel } from '../../styles/SignUp'
//import { InputLabel } from '../../styles/Date'

const options = [{ value: 'A', label: 'A' }, { value: 'B', label: 'B' }, { value: 'AB', label: 'AB' }, { value: 'O', label: 'O' }];
const optionsRH = [ { value: 'Negativo', label: 'Negativo' }, { value: 'Positivo', label: 'Positivo' }];
const optionsIllness = [{ value: 'Hipertension arterial', label: 'Hipertension arterial' }, { value: 'Diabetes', label: 'Diabetes' }, 
                    { value: 'Hipo/Hiper Tiroidismo', label: 'Hipo/Hiper Tiroidismo' }, { value: 'Cancer de mama', label: 'Cancer de mama' },
                    { value: 'Cancer Cervicouterino', label: 'Cancer Cervicouterino' }, { value: 'Problemas cardiacos diversos', label: 'Problemas cardiacos diversos' }, { value: 'Ninguna', label: 'Ninguna' }]
const optionsRelativeIllness = [{ value: 'Hipertension arterial', label: 'Hipertension arterial' }, { value: 'Diabetes', label: 'Diabetes' },
                    { value: 'Hipo/Hiper Tiroidismo', label: 'Hipo/Hiper Tiroidismo' }, { value: 'Ninguna', label: 'Ninguna' }]
const optionsHabits = [{ value: 'Fuma', label: 'Fuma' }, { value: 'Bebidas alcoholicas', label: 'Bebidas alcoholicas' }, 
                    { value: 'Medicamentos controlados', label: 'Medicamentos controlados' }, { value: 'Drogas', label: 'Drogas' }, { value: 'Ninguno', label: 'Ninguno' }]
const optionsSex = [{ value: 'H', label: 'Hombre' }, { value: 'M', label: 'Mujer' }];

function Input({ label, value, setValue, id, number, textarea, type, height, widthLabel, value1, setValue1 }) {

    const handleInput = (e) => {
        if(number){ setValue(e.target.value.replace(/[^0-9]/g, '')) }
        else{ setValue(e.target.value) }
    }

    return (
        <InputContainer>
            <InputLabel htmlFor={id} width={widthLabel}>{label}</InputLabel>
            {id === "blood" ? (
                <>
                <Select
                    value={value}
                    onChange={setValue}
                    options={options}
                    className="select-signup margin"
                    classNamePrefix="select"
                    placeholder="Tipo de sangre"
                />
                <Select
                    value={value1}
                    onChange={setValue1}
                    options={optionsRH}
                    className="select-signup"
                    classNamePrefix="select"
                    placeholder="RH"
                />
                </>
            ):id === "illness" ? (
                <Select
                    value={value}
                    onChange={setValue}
                    options={optionsIllness}
                    className="select-illness"
                    classNamePrefix="select"
                    placeholder="Enfermedades padecidas"
                    isMulti
                />
            ):id === "relativeIllness" ? (
                <Select
                    value={value}
                    onChange={setValue}
                    options={optionsRelativeIllness}
                    className="select-illness"
                    classNamePrefix="select"
                    placeholder="Padecimientos familiares"
                    isMulti
                />
            ):id === "habits" ? (
                <Select
                    value={value}
                    onChange={setValue}
                    options={optionsHabits}
                    className="select-illness"
                    classNamePrefix="select"
                    placeholder="Habitos"
                    isMulti
                />
            ):id === "sex" ?(
                <Select
                    value={value}
                    onChange={setValue}
                    options={optionsSex}
                    className="select-illness"
                    classNamePrefix="select"
                    placeholder="Sexo"
                />
            ):(
                textarea ? (
                    <TextareaForm 
                        value={value}
                        onChange={handleInput}
                        id={id}
                        height={height}
                    />
                ):(
                    <InputForm 
                        value={value}
                        onChange={handleInput}
                        id={id}
                        type={type || 'text'}
                    />
                )
            )}
        </InputContainer>
    )
}

export default Input
