import React, { useState } from 'react'
import axios from 'axios'
import Loader from 'react-loader-spinner'

import PatientItem from './PatientItem'
import CreatePDF from '../dateInfo/CreatePDF'

import { DoctorsGroupContentContainer } from '../../styles/Doctors'

import { URL } from '../../variables'

function History({ dates, setErr, setMessage, setTokenExpirate, user, seeGraphics }) {

    const [date, setDate] = useState({})
    const [params, setParams] = useState([])
    const [results, setResults] = useState([])
    const [services, setServices] = useState([])
    const [methods, setMethods] = useState([])
    const [refs, setRefs] = useState([])
    const [doctor, setDoctor] = useState('')
    const [loading, setLoading] = useState(false)
    const [success, setSuccess] = useState(false)

    const getDate = async (id, servID) => {
        const res = await axios({
            url: `${URL}/cita/${id}?serv_id=${servID}`,
            method: 'GET'
        })

        return res.data
    }

    const getResults = async (id, servID) => {
        const res = await axios({
            url: `${URL}/admin/client/results?id=${id}&serv_id=${servID}`,
            method: 'GET'
        })

        return res.data
    }

    const setDataRefs = (params, lastResults) => {
        let newResults = [...results]
        let newRefs = [...refs]
        for(let i = 0; i < params?.length; i++){
            let refsArray = []
            let resultsArray = []
            for(let j = 0; j < params[i]?.params?.length; j++){
                const res = lastResults.find(item => item.param === params[i]?.params[j].id) 
                resultsArray.push(res.resultado)
                refsArray.push({ min: res.minRef || params[i].params[j].minRef?.toString(), max: res.maxRef || params[i].params[j].maxRef?.toString(), id: params[i].params[j].id }) 
            }
            newResults.push({ results: resultsArray, service: params[i].service })
            newRefs.push({ refs: refsArray, service: params[i].service })
        }
        setResults(newResults)
        setRefs(newRefs)
    }

    const setDataPrint = (date, params, results) => {
        setResults(results)
        setParams(params)
        setServices(date)
        setDoctor(results[0].medico)
        setMethods(results[0].metodos.split(',').map(item => {return { value: item.trim(), label: item.trim() }}))
        setDataRefs(params, results)
    }

    const printResults = datePrint => {
        if(!loading){
            setLoading(true)
            setErr(false)
            getDate(datePrint.id, datePrint.servId).then( ({ date, params, results }) => {
                setDate({...datePrint, ...user, id: datePrint.id, doctor: date[0].doctor})
                setDataPrint(date, params, results)
                setLoading(false)
                setSuccess(true)
            }).catch( e => {
                console.log(e)
                setLoading(false)
                if(e.response?.status === 401){ return setTokenExpirate(true) }
                setErr(true)
                setMessage('Ha ocurrido un error intentado imprimir los resultados de esta cita. Intentalo más tarde o reporta el problema')
            })
        }
    }

    const fetchGraphics = date => {
        if(!loading){
            setLoading(true)
            setErr(false)
            console.log(date)
            getResults(date.cliente, date.servId).then( ({ params, results }) => {
                console.log(params)
                console.log(results)
                seeGraphics(params, results, date.servnombre)
            }).catch( e => {
                console.log(e)
                setLoading(false)
                if(e.response?.status === 401){ return setTokenExpirate(true) }
                setErr(true)
                setMessage('Ha ocurrido un error intentado imprimir los resultados de esta cita. Intentalo más tarde o reporta el problema')
            })
        }
    }

    const printDone = () => setSuccess(false)

    return (
        <DoctorsGroupContentContainer 
            height='fit-content'
            width="80%"
        >
            {loading && <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>}
            {dates.map((date, idx) => (
                <PatientItem 
                    key={date.id + Math.random().toString()}
                    idx={idx}
                    date={date}
                    history
                    printResults={printResults}
                    seeGraphics={fetchGraphics}
                />
            ))}
            {success && services.map((service, idx) => (
                <CreatePDF 
                    id={date.id}
                    name={date.nombre}
                    date={date.fecha}
                    age={date.edad}
                    doctor={doctor}
                    labDoctor={date.doctor}
                    servname={service.servnombre}
                    methods={methods}
                    results={results.find(item => item.service === service.servId)}
                    refs={refs.find(item => item.service === service.servId)}
                    params={params.find(item => item.service === service.servId)}
                    key={idx}
                    done={printDone}
                />
            ))}
        </DoctorsGroupContentContainer>
    )
}

export default History
