import React, { useState, useEffect } from 'react'
import { faChevronDown, faChevronLeft } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import PatientItem from './PatientItem'

import { DoctorsGroupContainer, DoctorsTitleContainer, DoctorsTitleButton, DoctorsGroupContentContainer } from '../../styles/Doctors'

function PatientsGroup({ dates, title, itemHeight, width, height, handleItem, aproveDates, setSessionExpirate, removeDate, setLoading }) {

    let interval = null

    const [showContent, setShowContent] = useState(true)
    const [changeIcon, setChangeIcon] = useState(true)
    const [rejectDate, setRejectDate] = useState(false)
    
    const handleBtnShowContent = (e) => {
        e.preventDefault()
        setShowContent(!showContent)
    } 

    useEffect(() => {
        interval = setTimeout(() => setChangeIcon(showContent), 500);
        return () => clearTimeout(interval)
    }, [showContent])

    return (
        <DoctorsGroupContainer width={width} height={height}>
            {title && (
                <DoctorsTitleContainer>
                    <h1>{title}</h1>
                    <FontAwesomeIcon icon={faChevronDown} color="white" size="3x"/>
                    <DoctorsTitleButton onClick={handleBtnShowContent}>
                        <FontAwesomeIcon 
                            icon={changeIcon ? faChevronDown : faChevronLeft} 
                            color="rgb(0,47,91)" 
                            size="2x" 
                            className="arrow-icon"
                        />
                    </DoctorsTitleButton>
                </DoctorsTitleContainer>
            )}
            <DoctorsGroupContentContainer 
                height={height ? 'fit-content' : showContent ? `${itemHeight * dates.length}vw` : '0'}
                width={width}
            >
                {dates.map( (date, idx) => (
                    <PatientItem 
                        key={date.id + Math.random().toString()}
                        idx={idx}
                        date={date}
                        onClick={handleItem}
                        aproveDates={aproveDates}
                        setSessionExpirate={setSessionExpirate}
                        removeDate={removeDate}
                        setRejectDate={setRejectDate}
                        setLoading={setLoading}
                    />  
                ))}
            </DoctorsGroupContentContainer>
        </DoctorsGroupContainer>
    )
}

export default PatientsGroup
