import React from 'react'
import { Page, Text, View, Document, StyleSheet, Image, Font } from '@react-pdf/renderer';

Font.register({ family: 'Montserrat', src: 'http://localhost:3000/static/media/Montserrat-Regular.ee653992.ttf' });
Font.register({ family: 'MontserratBold', src: 'http://localhost:3000/static/media/Montserrat-Bold.ade91f47.ttf' });
Font.register({ family: 'MontserratLight', src: 'http://localhost:3000/static/media/Montserrat-Light.409c7f79.ttf' });
Font.register({ family: 'MontserratMedium', src: 'http://localhost:3000/static/media/Montserrat-Medium.c8b6e083.ttf' });

const styles = StyleSheet.create({
    page: {
        flexDirection: 'column',
        alignItems: 'center',
        paddingVertical: '1%'
    },
    section: {
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'space-between',
        width: '100%',
        paddingHorizontal: '5%'
    },
    logo: {
        width: 200
    },
    textBold: {
        fontFamily: 'MontserratBold',
        fontSize: 15
    },
    textMedium: {
        fontFamily: 'MontserratMedium',
        fontSize: 20
    },
    textLight: { 
        fontFamily: 'MontserratLight',
        fontSize: 15
    },
    text: {
        fontFamily: 'Montserrat',
        fontSize: 15
    },
    textContainer:{
        flexDirection: 'row',
        alignContent: 'center'
    },
    table:{
        width: '100%',
        flexDirection: 'row',
        height: '55%',
        paddingHorizontal: '5%'
    },
    tableColumn:{
        width: '33%',
        flexDirection: 'column',
        alignItems: 'center',
    },
    tableHeader:{
        fontFamily: 'MontserratMedium',
        fontSize: 15, 
        borderBottomColor: '#002f5b', 
        borderBottomWidth: 5, 
        marginBottom: 5, 
        width: '100%', 
        textAlign: 'center'
    },
    line:{
        position: 'absolute',
        bottom: 0,
        width: '100%',
        height: '3%',
        backgroundColor: '#002f5b'
    }
});

const twoDigits = number => number < 10 ? `0${number}` : number

const getDate = (date) => {
    const nDate = new Date(date)
    return `${twoDigits(nDate.getDate())}/${twoDigits(nDate.getMonth() + 1)}/${nDate.getFullYear()}`
}

function PDF({ props }) {

    return (
        <Document>
            <Page size="A4" style={styles.page}>
                <View style={{...styles.section, paddingHorizontal: '5%', marginBottom: '5%', marginVertical: '2%'}}>
                    <Image 
                        src="https://res.cloudinary.com/treetech/image/upload/c_crop,h_65,w_420,x_405,y_40/v1609126987/mdmedica/email-header_ezpimg.jpg"
                        style={styles.logo}
                    />
                    <Text style={styles.textBold}>Folio: {props.id}</Text>
                </View>
                <Text style={{...styles.textBold, fontSize: 25}}>Asociación M.D. Médica</Text>
                <Text style={styles.textLight}>Resultados de Laboratorio</Text>
                <View style={{...styles.section, paddingHorizontal: '5%', marginBottom: '2%', justifyContent: 'flex-start', marginTop: '2%' }}>
                    <View style={{...styles.textContainer, width: '70%'}}>
                        <Text style={{...styles.textBold, marginRight: '2%'}}>Paciente:</Text>
                        <Text style={styles.text}>{props.name}</Text>
                    </View>
                    <View style={styles.textContainer}>
                        <Text style={{...styles.textBold, marginRight: '2%'}}>Fecha:</Text>
                        <Text style={styles.text}>{getDate(props.date)}</Text>
                    </View>
                </View>
                <View style={{...styles.section, paddingHorizontal: '5%', marginBottom: '2%', justifyContent: 'flex-start' }}>
                    <View style={{...styles.textContainer, width: '70%'}}>
                        <Text style={{...styles.textBold, marginRight: '2%'}}>Médico:</Text>
                        <Text style={styles.text}>{props.doctor}</Text>
                    </View>
                    <View style={styles.textContainer}>
                        <Text style={{...styles.textBold, marginRight: '2%'}}>Edad:</Text>
                        <Text style={styles.text}>{props.age}</Text>
                    </View>
                </View>
                <Text style={styles.textMedium}>{props.servname}</Text>
                <View style={styles.table}>
                    <View style={styles.tableColumn}>
                        <Text style={styles.tableHeader}>Análisis</Text>
                        {props.params.params.map((param, idx) => (
                            <Text style={{...styles.textLight, marginBottom: 2, color: '#646464', width: '100%'}} key={idx}>{param.nombre}</Text>
                        ))}
                    </View>
                    <View style={styles.tableColumn}>
                        <Text style={styles.tableHeader}>Resultado</Text>
                        {props.params.params.map((param, idx) => (
                            <Text style={{...styles.textLight, marginBottom: 2, color: '#646464'}} key={idx}>{props.results.results[idx]} {param.unidades}</Text>
                        ))}
                    </View>
                    <View style={styles.tableColumn}>
                        <Text style={styles.tableHeader}>Referencia</Text>
                        {props.params.params.map((param, idx) => (
                            <Text style={{...styles.textLight, marginBottom: 2, color: '#646464'}} key={idx}>{props.refs.refs[idx].min} - {props.refs.refs[idx].max} {param.unidades}</Text>
                        ))}
                    </View>
                </View>
                <View style={{...styles.section, paddingHorizontal: '5%', justifyContent: 'flex-start', marginTop: '2%' }}>
                    <View style={styles.textContainer}>
                        <Text style={{...styles.textBold, marginRight: '2%'}}>Método:</Text>
                        <Text style={styles.text}>{props.methods.map(item => item.value).join(', ')}</Text>
                    </View>
                </View>
                <View style={{...styles.section, paddingHorizontal: '5%', marginBottom: '5%', marginVertical: '2%'}}>
                    <View style={{ flexDirection: 'column', alignItems: 'center' }}>
                        <Text style={{...styles.textMedium, fontSize: 10}}>{props.labDoctor}</Text>
                        <Text style={{...styles.textMedium, fontSize: 10}}>PROCESO DE LABORATORIO</Text>
                    </View>
                    <Image 
                        src="https://res.cloudinary.com/treetech/image/upload/v1612647542/mdmedica/firma-resultados_anz3ax.png"
                        style={{ width: 100 }}
                    />
                </View>
                <Text style={{...styles.textLight, fontSize: 10}}>Calzada Saltilla 400 #899, Col. Ampliación La Rosita: Torreón Coahuila.</Text>
                <Text style={{...styles.textLight, fontSize: 10}}>C.P. 87258 Tel. 8713778480</Text>
                <View style={styles.line}></View>
            </Page>
        </Document>
    )
}

export default PDF
