import React, { useState } from 'react'
import { Line } from 'react-chartjs-2';

import { GraphicContainer } from '../../styles/Admin'

function Graphic({ param, results }) {

    const width = window.screen.width
    const height = window.screen.height

    const [clicked, setClicked] = useState(false)

    const twoDigits = number => number < 10 ? `0${number}` : number

    const getDate = (date) => {
        const nDate = new Date(date)
        return `${twoDigits(nDate.getDate())}/${twoDigits(nDate.getMonth() + 1)}/${nDate.getFullYear()}`
    }
    const handleItem = () => setClicked(!clicked)

    return (
        <GraphicContainer 
            onClick={handleItem}
            width={clicked ? '100%' : '30%'}
            height={clicked ? '70vh': '22vh'}
        >
            <span>{param.nombre}</span>
            <Line 
                data={{
                    labels: results.map(item => getDate(item.fecha)),
                    datasets: [{
                        label: param.unidades,
                        data: results.map(item => item.resultado),
                        backgroundColor: ['rgba(0, 0, 0, 0)'],
                        borderColor: ['rgba(0,47,91,1)']
                    }]
                }}
                options={{
                    responsive: true,
                    maintainAspectRatio: true,
                    scales: { yAxes: [{ ticks: {beginAtZero: true } }] }
                }}
            />
        </GraphicContainer>
    )
}

export default Graphic
