import React from 'react'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars, faTimes } from "@fortawesome/free-solid-svg-icons";

import Graphic from './Graphic'

import { DoctorsGroupContentContainer } from '../../styles/Doctors'
import { GraphicsContainer, GraphicTitle } from '../../styles/Admin'

function Graphics({ params, results, servname, closeGraphics }) {

    return (
        <DoctorsGroupContentContainer 
            height='fit-content'
            width="80%"
        >
            <GraphicTitle>
                <div onClick={closeGraphics}>
                    <FontAwesomeIcon icon={faTimes} color="rgb(0,47,91)" />
                </div>
                <h2>{servname}</h2>
                <div></div>
            </GraphicTitle>
            <GraphicsContainer>
                {params.map(param => (
                    <Graphic 
                        results={results.find(item => item.param === param.id).results}
                        param={param}
                        key={param.id}
                    />
                ))}
            </GraphicsContainer>
        </DoctorsGroupContentContainer>
    )
}

export default Graphics
