import React, { useState, useEffect } from 'react'
import Loader from 'react-loader-spinner'
import axios from 'axios'
import Select from 'react-select'

import TokenExpirate from '../TokenExpirate'
import ResultItem from './ResultItem'
import CreatePDF from './CreatePDF'

import { ContainerForm, InfoContainer, EditBtn, Message, LoadingContainer, InfoItem } from '../../styles/User'
import { TableResults } from '../../styles/Admin'

import { URL } from '../../variables'

const options = [{ value: 'Enzimatico', label: 'Enzimatico' },{ value: 'Colorimetrico', label: 'Colorimetrico' }]

function Results({ params, id, lastResults, date, services }) {

    const [results, setResults] = useState([])
    const [refs, setRefs] = useState([])
    const [numberLabs, setNumberLabs] = useState(1)
    const [currentLab, setCurrentLab] = useState(0)
    const [err, seteErr] = useState(false)
    const [loading, setLoading] = useState(false)
    const [tokenExpirate, setTokenExpirate] = useState(false)
    const [success, setSuccess] = useState(false)
    const [messageErr, setMessageErr] = useState('')
    const [doctor, setDoctor] = useState('')
    const [methods, setMethods] = useState([])
    const [methodsSelect, setMethodsSelect] = useState(null)

    const handleInputDoctor = e => setDoctor(e.target.value)
    const handleSelect = value => setMethodsSelect(value)

    const handleInput = (idx, value) => {
        let newResults = [...results]
        newResults[currentLab].results.splice(idx, 1, value)
        setResults(newResults)
    }

    const handleInputMin = (idx, value) => {
        let newRefs = [...refs]
        const lastValue = newRefs[currentLab].refs[idx]
        newRefs[currentLab].refs.splice(idx, 1, { ...lastValue, min: value, })
        setRefs(newRefs)
    }

    const handleInputMax = (idx, value) => {
        let newRefs = [...refs]
        const lastValue = newRefs[currentLab].refs[idx]
        newRefs[currentLab].refs.splice(idx, 1, { ...lastValue, max: value })
        setRefs(newRefs)
    }

    const postResults = async (data) => {
        const res = await axios({
            url: `${URL}/cita/results`,
            method: 'POST',
            data
        })

        return res.data
    }

    const saveResults = (newMethods) => {
        seteErr(false)
        setLoading(true)
        postResults({ results, refs, id, doctor, methods: newMethods }).then(() => {
            setLoading(false)
            setMessageErr('Se han guardado los resultados correctamente.')
            setSuccess(true)
        }).catch(e => {
            console.log(e)
            setLoading(false)
            if(e.reponse?.status === 401){ return setTokenExpirate(true) }
            seteErr(true)
            setMessageErr('Ha ocurrido un error intentando guardar los resultados. Intentalo más tarde o reporta el problema')
        })
    }

    const handleBtn = e => {
        e.preventDefault()
        if(checkData()){
            let newMethods = [...methods]
            newMethods[currentLab] = methodsSelect
            setMethods(newMethods)
            setMethodsSelect(null)
            seteErr(false)
            if(currentLab + 1 === numberLabs){ saveResults(newMethods) }
            else{ setCurrentLab(currentLab + 1)  }
        }
    }

    const checkData = () => {
        const isSomeResultEmpty = results[currentLab].results.some( item => item === "" )
        const isSomeRefEmpty= refs[currentLab].refs.some( ref => ref.min === "" || ref.max === "")
        if(isSomeResultEmpty || isSomeRefEmpty || doctor === "" || !methodsSelect){
            seteErr(true)
            setMessageErr('No puede dejar campos en blanco.')
            return false
        }
        return true
    }

    const setData = () => {
        setNumberLabs(params?.length)
        let newResults = [...results]
        let newRefs = [...refs]
        for(let i = 0; i < params?.length; i++){
            newResults.push({ results: [''], service: params[i].service })
            let refsArray = []
            for(let j = 0; j < params[i]?.params?.length; j++){ 
                refsArray.push({ min: params[i].params[j].minRef?.toString(), max: params[i].params[j].maxRef?.toString(), id: params[i].params[j].id }) 
            }
            newRefs.push({ refs: refsArray, service: params[i].service })
        }
        setResults(newResults)
        setRefs(newRefs)
    }

    const printDone = () => setSuccess(false)

    useEffect(() => {
        setData()
    }, [params])

    useEffect(() => {
        if(methods[currentLab]){ setMethodsSelect(methods[currentLab]) }
    }, [currentLab])

    useEffect(() => {
        if(lastResults[0]){
            let newMethods = []
            for(let i = 0; i < lastResults.length; i++){
                newMethods.push(lastResults[0].metodos.split(',').map(item => { return { value: item.trim(), label: item.trim() } }))
            }
            setDoctor(lastResults[0].medico)
            setMethods(newMethods)
            setMethodsSelect(newMethods[0])
        }
    }, [lastResults])

    return (
        <ContainerForm>
            <InfoContainer width="100%" alignItems="center">
                <h2>{services[currentLab]?.servnombre}</h2>
                <InfoItem 
                    alignItems="center" 
                    labelWidth="10vw"
                >
                    <label htmlFor='doctor'>Médico: </label>
                    <input 
                        id='doctor'
                        value={doctor}
                        onChange={handleInputDoctor}
                    />
                </InfoItem>
                <InfoItem 
                    alignItems="center"
                    labelWidth="41.5vw" 
                >
                    <label htmlFor='doctor'>Métodos: </label>
                    <Select 
                        options={options}
                        value={methods[currentLab] || methodsSelect}
                        onChange={handleSelect}
                        isMulti
                        className="select-illness"
                    />
                </InfoItem>
                <TableResults>
                    <thead>
                        <tr>
                            <th>Análisis</th>
                            <th>Resultado</th>
                            <th>Referencia</th>
                        </tr>
                    </thead>
                    <tbody>
                        {results.length !== 0 && refs.length !== 0 && params[currentLab]?.params?.map( (param, idx) => ( 
                            <ResultItem 
                                key={idx}
                                param={param}
                                idx={idx}
                                onChange={handleInput}
                                handleInputMin={handleInputMin}
                                handleInputMax={handleInputMax}
                                lastResults={lastResults}
                            />
                        ) )}
                    </tbody>
                </TableResults>
                <LoadingContainer>
                    {(err || success) && (<Message color={success ? 'rgb(0,47,91)' : 'red'}>{messageErr}</Message>)}
                    {loading && <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>}
                </LoadingContainer>
                <EditBtn margin="0" onClick={handleBtn}>{currentLab + 1 === numberLabs ? 'GUARDAR RESULTADOS' : 'SIGUIENTE'}</EditBtn>
                {success && services.map((service, idx) => (
                    <CreatePDF 
                        id={id}
                        name={date.nombre}
                        date={date.fecha}
                        age={date.edad}
                        doctor={doctor}
                        labDoctor={date.doctor}
                        servname={service.servnombre}
                        methods={methods[idx]}
                        results={results.find(item => item.service === service.servId)}
                        refs={refs.find(item => item.service === service.servId)}
                        params={params.find(item => item.service === service.servId)}
                        key={idx}
                        done={printDone}
                    />
                ))}
            </InfoContainer>
            <TokenExpirate modal={tokenExpirate} admin/>
        </ContainerForm>
    )
}

export default Results
